import React, { Component } from 'react';
import $ from 'jquery';

class InquiryForm extends Component {
    componentDidMount() {
        // 1:1 :  
        $(".inquiry-file__btn").click(function (e) {
            e.preventDefault();
            if ($(this).hasClass('file-add') == true) {
                $("#iFile").click();
            }
        });
        $('.input-file').on('change', function (e) {
            const $add = 'file-add',
                  $remove = 'remove-btn';
            let name = e.target.files[0].name,
                add_remove = $('.inquiry-file__btn'),
                more_btn = $('.inquiry-file__btn.' + $add),
                remove_btn = $('.inquiry-file__btn.' + $remove),
                inq_name = $('.inquiry-file__name');
            $('.inquiry-file__name', $(e.target).parent()).val(name);
            if ($('.inquiry-file__name', '.input-file').val() == "") {
                add_remove.addClass($add).removeClass($remove);
            }
            else {
                add_remove.removeClass($add).addClass($remove);
                inq_name.css('display', 'inline-block');
                $('.inquiry-file__btn.' + $remove).click(function () {
                    $('.input-file').val('');
                    $('.inquiry-file__name', $(e.target).parent()).val('');
                    add_remove.addClass($add).removeClass($remove);
                    inq_name.hide();
                });
            }
        });
    }
    changeToList = () => {
        this.props.onCreate(false);
    }
    render() {
        return(
            <section className="inquiry-form__wrap">
                <h4 className="k-sub__title">1:1 </h4>
                <form className="inquiry-form">
                    <ul className="inquiry-form__list">
                        <li>
                            <label for="" className="inquiry-form__label"></label>
                            <select id="" className="inquiry-form__select">
                                <option default="default" hidden="hidden">  </option>
                                <option></option>
                                <option>/</option>
                                <option>/</option>
                                <option>/</option>
                                <option></option>
                            </select>
                        </li>
                        <li>
                            <label for="iTitle" className="inquiry-form__label"></label>
                            <input type="text" id="iTitle" placeholder=" .(20 )" className="inquiry-form__input"/>
                        </li>
                        <li>
                            <label for="iName" className="inquiry-form__label"></label>
                            <input type="text" id="iName" value="" placeholder=" ." className="inquiry-form__input"/>
                        </li>
                        <li>
                            <label for="iEmail" className="inquiry-form__label"></label>
                            <input type="email" id="iEmail" value="fotrise@naver.com" placeholder="  ." className="inquiry-form__input"/>
                        </li>
                        <li>
                            <label for="iContent" className="inquiry-form__label"></label>
                            <textarea id="iContent" placeholder="  . (500  .)
         " className="inquiry-form__textarea"></textarea>
                        </li>
                        <li>
                            <label for="iFile" className="inquiry-form__label"></label>
                            <input type="file" id="iFile" className="inquiry-form__input input-file"/>
                            <div className="inquiry-file__box">
                                <p className="inquiry-file__subject"> </p>
                                <a href="javascipt:void(0);" className="inquiry-file__btn file-add txt-hidden">
                                      
                                </a>
                                <input type="text" readOnly="readOnly" className="inquiry-file__name k-truncate"/>
                            </div>
                        </li>
                    </ul>
                    <div className="k-btns">
                        <button className="k-btn cancel-btn" onClick={this.changeToList}></button>
                        <button className="k-btn" type="submit"></button>
                    </div>
                </form>
            </section>
        );
    }
}

export default InquiryForm;