import React, { Component } from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';

class SideMenu extends Component {
    componentDidMount() {
        const $input = $(".k-form__input");
        const $label = ".k-form__label";
        $input.focus(function() {
            $(this).siblings($label).addClass('active'); 
        });
        $input.blur(function() {
            let thisVal = $(this).val();
            if(!thisVal) {
                $(this).siblings($label).removeClass('active');
            }
        });
    }

    closeSideMenu = () => {
        $(".k-sidemenu__wrap").fadeOut(300);
        $(".k-sidemenu").removeClass("active");
    }

    render() {
        return (
            <div>
                <section className="k-sidemenu__wrap">
                    <article className="k-dimmer" onClick={this.closeSideMenu}></article>
                    <article className="k-sidemenu">
                    <a href="javascript:void(0)" class="k-sidemenu__close txt-hidden" onClick={this.closeSideMenu}>
                         </a>
                        <form className="k-form k-sidemenu__form clearfix">
                            <div className="form-input__box">
                                <input id="sideID" type="text" name="ID" className="k-form__input" />
                                <label for="sideID" className="k-form__label">()</label>
                            </div>
                            <div className="form-input__box">
                                <input id="sidePW" type="password" name="PW" className="k-form__input" />
                                <label for="sidePW" className="k-form__label"></label>
                            </div>
                            <button className="k-form__btn k-btn full"></button>
                            <input id="idSave" type="checkbox" className="k-check k-hidden"/>
                            <label for="idSave" className="k-check__label"> </label>
                            <nav className="k-client__nav">
                                <a href="javascript:void(0)"></a>
                                <a href="javascript:void(0)">ID/PW </a>
                            </nav>
                        </form>
                        <nav className="k-sidemenu__nav">
                            <Link to="/notice"></Link>
                            <a href="#"></a>
                            <a href="#"></a>
                            <a href="#"></a>
                            <a href="#">FAQ</a>
                            <a href="#"> </a>
                        </nav>
                    </article>
                </section>
            </div>
        );
    }
}

export default SideMenu;