import React, { Component } from 'react';
import $ from 'jquery';

class Reserv1 extends Component {
    setNumber = (e) => {
        this.props.onChange(e.target.value);
    }
    render() {
        const { data } = this.props;
        return(
            <section className="reserv-step reserv-step__1">
                <div className="reserv-wrap">
                    <h2 className="reserv-step__title">  </h2>
                    <p className="reserv-step__alert">*     (1899-3197)</p>
                    <select className="reserv-step__select" value={data.number} onChange={this.setNumber}>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                    </select>
                </div>
            </section>
        );
    }
}

export default Reserv1;